<template>
  <section class="pop-start"
    v-show="isShow">
    <div class="box-logo">
      <img :src="ImageSource.url.logo" alt="">
    </div>
    <div class="primary-red">Web Edition</div>
    <div class="box-creator">
      <div class="text-center">Jeremiah LeeYoungbeen</div>
      <div class="text-center">All game program copyright reserved by Youngbeen 2024-present</div>
    </div>
    <div class="desc style-font" :data-content="desc">
      {{ desc }}
    </div>
    <div class="select-player text-center">
      <div class="box-player"
        :class="[selectedPlayer === '1' && 'active']"
        @click="togglePlayer('1')">
        <div class="avatar char-backpacker"></div>
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '2' && 'active']"
        @click="togglePlayer('2')">
        <div class="avatar char-sportgirl"></div>
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '3' && 'active']"
        @click="togglePlayer('3')">
        <div class="avatar char-oldman"></div>
      </div>
    </div>
    <div class="select-player text-center">
      <div class="box-player"
        :class="[selectedPlayer === '4' && 'active']"
        @click="togglePlayer('4')">
        <div class="avatar char-soldier"></div>
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '5' && 'active']"
        @click="togglePlayer('5')">
        <div class="avatar char-boxer"></div>
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '6' && 'active']"
        @click="togglePlayer('6')">
        <div class="avatar char-fatboy"></div>
      </div>
    </div>
    <div class="char-desc">
      {{ characterDesc }}
    </div>
    <div class="text-center" style="margin-top: 20px;">
      <button class="primary-button start-button"
        @click="start()"></button>
    </div>
  </section>
</template>

<script>
import eventBus from '@/EventBus'
import player from '@/models/Player'
import ImageSource from '@/models/config/ImageSource'

const PlayerMap = {
  1: 'backpacker',
  2: 'sportgirl',
  3: 'oldman',
  4: 'soldier',
  5: 'boxer',
  6: 'fatboy'
}
const characterDescMap = {
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '+4'
}
export default {
  data () {
    return {
      isShow: true,
      desc: '12...',
      selectedPlayer: '1',
      ImageSource
    }
  },
  computed: {
    characterDesc () {
      return characterDescMap[this.selectedPlayer]
    }
  },

  mounted () {
    eventBus.$on('notifyMessageAction', ({ action, tag }) => {
      if (action === 'confirm' && tag && tag.source === 'restart') {
        this.isShow = true
      }
    })
  },

  beforeDestroy () {
    eventBus.$off('notifyMessageAction')
  },

  methods: {
    togglePlayer (val) {
      this.selectedPlayer = val
    },
    start () {
      player.character = PlayerMap[this.selectedPlayer]
      this.isShow = false
      this.selectedPlayer = '1'
      eventBus.$emit('notifyStart')
    }
  }
}
</script>

